:root{
    --main-font: 'Montserrat', sans-serif;
    --cursive-font: 'Gloria Hallelujah', cursive;
    --green: #AABD40;
    --dark-blue: #448FAE;
    --light-blue: #66B1D0;
    --electric-blue: #4E8FED;
    --red: #DB6A5B;
    --shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

*,
*::before,
*::after{
    box-sizing: border-box;
}

html{
    font-size: 62.5%;
}

body{
    font-family: var(--main-font);
    font-size: 1.6rem;
    padding: 2rem 0;
    padding-bottom: 0;
    margin: 0;
}

p{
    margin-top: 0;
    line-height: 1.4;
}

.hidden{
    display: none;
}

.loader{
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    left: 0;
    z-index: 999999;
    background: var(--green);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    padding: 2rem;
}

.loader p{
    font-size: 2rem;
    text-align: center;
}